.Notifications {
    border: 1px dashed #FF5A5F;
    padding: 20px;
    margin: 10px;

}

.Notifications p {
    margin-bottom: 15px;
}

li {
    margin-left: 40px;
    list-style-type: disc;
}

li[data-priority="default"] {
    color: blue;
}

li[data-priority="urgent"] {
    color: red;
}

strong {
    font-weight: 900;
}